<template>
<div>
  <div v-if="source"  style="padding:20px;border-bottom:1px solid #d1dbe5;">
    <a href="/foreground/mngview/fieldData?source=mnglist" style="color:#409EFF;font-size:16px;">返回</a>
  </div>
  <div class="container" v-loading.body="loading"  element-loading-text="拼命加载中">

  	<div id="nav-menu">
  		<el-tabs v-model="activeName" type="border-card">
  			<el-tab-pane label="全部" name="all">
  				<!-- fiter condition -->
  				<el-row class="filter-wrap">
  					<el-col :span="7">
  							<div class="grid-content">
  								<h3>共{{pageParam.total}}个客户</h3>
  							</div>
  					</el-col>
  					<el-col :span="7">
  						<div class="grid-content" >
  							<el-select v-model="filter" @change="setFilterParam" key="all-filter">
  								<el-option
  									v-for="item in filters"
  									:key="item.value"
  									:label="item.label"
  									:value="item.value">
  								</el-option>
  							</el-select>
  						</div>
  					</el-col>
  					<el-col :span="10">
  						<div class="grid-content">
  							<el-select v-model="rank" class="rank-list" @change="setRankParam" key="all-rank">
  								<el-option
  									v-for="item in ranks"
  									:key="item.value"
  									:label="item.label"
                    :name="item.name"
  									:value="item.value">
  								</el-option>
  							</el-select>
  						</div>
  					</el-col>
  				</el-row>
  				<!-- client list -->
  				<el-menu default-active="" class="client-list" >
              <el-menu-item  v-for="(val, key) in clientList"
                :class="{hackActive:$route.path==('/foreground/mnglist/mngdetail/'+val.id)}"
                :index="''+key">
                <router-link :to="'/foreground/mnglist/mngdetail/'+val.id">
                  <el-col :span="18">
                    <div class="clearfix">
                      <el-col :span="5">
                        <el-tag type="danger">{{val.identity}}</el-tag>
                      </el-col>
                      <el-col :span="5">
                        {{val.name}}
                      </el-col>
                      <el-col :span="13" class="company-name">
                        <span>
                          {{val.company}}&nbsp;
                        </span>

                      </el-col>

                    </div>
                    <div  class="clearfix info-btm">

                      <el-col :span="12">
                        总成交金额：{{val.total_price}}元
                      </el-col>
                      <el-col :span="12">
                        最近成交时间：{{val.deal_time}}
                      </el-col>
                    </div>
                  </el-col>
                  <el-col :span="6">
                      <el-button size="small" type="primary" class="client-check-btn">{{val.is_created == 0? '创建客户': '查看信息'}}</el-button>
                  </el-col>
                </router-link>
              </el-menu-item>

  				</el-menu>

          <!-- pagination -->
          <el-pagination
       		 @current-change="handleCurrentChange"
       		 :page-size="10"
       		 :current-page.sync="customerParam.page"
       		 layout="total, prev, pager, next"
       		 :total="pageParam.total"
       		 style="margin-top:20px;"
       		 >
       	 </el-pagination>
        </el-tab-pane>

  		</el-tabs>

  	</div>
    <div id="content_container" >
      <router-view ></router-view>
    </div>
  </div>
</div>
</template>
<script>
export default {
	name: 'UserList',
	data: function () {
		return {
      source:false,
      loading: true,
			activeName: 'all',
			filter: "全部客户",
			rank: "默认排序",
			selectChange:false,
			customerParam:{
				"page": 1,
        "type": 1,
        "from": "customer_field_list",
			},
      pageParam: {},
			filters: [{
				 value: '0',
				 label: '全部客户'
			 }, {
				 value: '1',
				 label: '决策人'
			 }, {
				 value: '2',
				 label: '非决策人'
			 }],
			 ranks: [{
					value: '0',
					label: '默认排序',
          name:''
				},{
					value: '1',
					label: '按成交金额排序',
          name: 'total_price',

				}, {
					value: '2',
					label: '按最近成交排序',
          name: 'deal_time'
				}],
				clientList:{},
				pagination:{},
		}
	},
	mounted () {
		let self = this;
		// 判断页面来源，根据来源请求参数不同
		if(self.$route.query.source == 'fieldData') {
      self.source = true;
			//let fromName =  self.$route.query.from ;
			// if (fromName == 'boss' || fromName == 'bossAdd') {
			// 	let mobile = self.$route.query.mobile
			// 	let companyId = self.$route.query.companyId
      //
			// 	if (self.$route.query.type) {
			// 		self.customerParam['type'] = self.$route.query.type;
			// 	}
			// 	if (self.$route.query.checkType) {
			// 		self.customerParam['checkType'] = self.$route.query.checkType;
			// 	}
			// 	self.customerParam['from'] = fromName;
			// 	self.customerParam['mobile'] = mobile;
			// 	self.customerParam['company_id'] = companyId;
			// }
		}
		this.getClientList()
		this.$root.Bus.$on('addSuc', (value) => {
			let vMobile = value[0];
			let vCompanyId = value[1];
			for (let v in self.clientList) {
				  let mobile = self.clientList[v]['mobile'];
				  let company_id = self.clientList[v]['company_id'];
					if (mobile == vMobile && company_id == vCompanyId){
						self.clientList[v]['is_created'] = 1;
					}
			}

		})
		self.$root.Bus.$on('tagDraft',() => { // 切换客户后，给客户打草稿标签
			let ls = window.localStorage
			for (let v in self.clientList) {
					let company_id = self.clientList[v]['company_id'];
					let mobile = self.clientList[v]['mobile'];
					let draftId = 'draft_'+mobile+company_id
					self.clientList[v]['is_draft'] = false;
					if (ls[draftId]) {
						self.clientList[v]['is_draft'] = true;
					}
			}
		});
		this.$bus.$off('flashUserList');
		this.$bus.$on('flashUserList',self.getClientList)
	},
	methods: {
		setFilterParam(v) {
        var self = this;
        self.customerParam.page = 1
				self.customerParam.identity = v;
				self.getClientList();
		},
		setRankParam(v) {
        var self = this;
			  self.customerParam.page = 1
        var name = v == 1? name="total_price": name="deal_time"
				self.customerParam['sort'] = name;
        self.customerParam['order'] = 1;
				self.getClientList();


		},
		getClientList(arg) {
			let self = this;

			self.$ajax.get(self.$api.getModuleHost('CRM', "/manage/getCustomerList"),self.customerParam).then(function (data) {
					self.selectChange = false;
					if (data.code == 1000) {
							self.$parent.listData = true; // 在父元素中控制列表数据的状态，如果有值就在右侧内容区域显示编辑或查看，没有则显示没有数据
              self.clientList = data.data;

              self.pageParam['total'] = data.total;
    					self.loading = false;

            let id = self.clientList[0]['id'];
            var source = '';
          if(self.$route.query.source == 'fieldData') { // 如果从管理按字段维度跳转至此页面 加此参数 让其可以显示返回按钮
            source =   '?source=fieldData'
          }
            self.$router.push({ path:`/foreground/mnglist/mngdetail/${id}${source}`})
          } else {
              console.log("currently has none clients' data");
          }
			}).catch(function (err) {
					console.error(err);
			});

		},
		 handleCurrentChange(val) {
      var self = this;
      self.loading = true;
			self.customerParam.page = val;
			self.getClientList();
    }
	},
}
</script>
<style lang="less">
  .container {
    display: flex;
    min-width: 1280px;
  }
  .pointer{
    cursor:pointer;
  }
  .el-form-item{
    margin-bottom: 14px;
  }
	#nav-menu {
		width: 500px;
		min-width: 500px;
		min-height:100vh;
		border-radius: 0;
		overflow-y:auto;
		background:#fff;
		border: 1px solid #d1dbe5;
    border-top:none;
		box-sizing:border-box;
		.el-menu {
			background-color: #fff;
		}
		.el-menu-item,
		.el-submenu .el-submenu__title {
			padding-top:10px;
			height:70px;
			line-height:20px;
			&:hover {
				background:#EFF2F7
			}
		}
		.info-btm{
			margin-top:10px;
		}
		.client-check-btn{
			float:right;
			margin-top:10px;
		}
		.filter-wrap{
				padding:12px 20px;
				.grid-content{
					h3{
						line-height:36px;
					}
					.rank-list{
						margin-left:10px;
					}

				}
		}

	}
	.el-tabs--border-card>.el-tabs__content {
		padding:0;
	}
	.el-tabs--border-card{
		box-shadow:none;
		border:none;
	}
	.el-menu-item.is-active{
		background:#E5E9F2;
		color:#20A0FF;
	}
	.pagination{
		margin-top:20px;
	}
	.client-list li{
		a{
        font-size:12px;
				color:#333;
		}
		border-bottom:1px solid #d1dbe5
	}
	.company-name {
		text-overflow:ellipsis;
		white-space:nowrap;
		overflow:hidden;
	}
	.draft{
		color:#ff4949;
	}
  /*userListActiveHack*/
  .el-menu-item.is-active{
    background-color:transparent;
  }
  .el-menu-item.hackActive{
    background: #E5E9F2;
  }
  #content_container {
    min-height: 100vh;
    flex: 1 auto;
    position:relative;
    color:#333;
    padding:20px;

  }
</style>



